/* news item */
.news-item {
  height: 100%;
  border-radius: 0 !important;
  /* news item image */ }
  .news-item__image {
    height: 300px;
    position: relative; }
    .news-item__image::before {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 2;
      background: -webkit-gradient(linear, left bottom, left top, from(rgba(0, 0, 0, 0.4)), to(rgba(0, 0, 0, 0)));
      background: -webkit-linear-gradient(bottom, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0) 100%);
      background: -o-linear-gradient(bottom, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0) 100%);
      background: linear-gradient(360deg, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0) 100%); }
  .news-item a {
    color: var(--secondary-color);
    text-decoration: underline;
    font-weight: normal;
    -webkit-transition: color .15s ease-in-out, background-color .2s ease-in;
    -o-transition: color .15s ease-in-out, background-color .2s ease-in;
    transition: color .15s ease-in-out, background-color .2s ease-in; }
    .news-item a:hover {
      text-decoration: none;
      color: var(--primary-white);
      background-color: var(--secondary-color); }



